@import './header';
@import './title';
@import './reasons';
@import './footer';
// @import './footer.scss';
// @import './honey-products.scss';

:root {
  --active-link: #2196f3;
  --color-menu: #ecdcab;
  --color-title: #fcfcf7;
  --border-bottom-curent: #bf932a;
  --hover-menu-btn: #9e6200;
  --transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
  --font-hand: 'Dancing Script';
}

*,
*::before,
*::after {
  box-sizing: border-box;
  list-style-type: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

:root {
  --font-family: 'DM Sans';
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
